/* ModuleBox.css */

.module-box {
  width: 45%; /* Set the width to 45% of the screen */
  border: 1px solid #ddd;
  padding: 20px;
  margin: 10px;
  box-sizing: border-box; /* Include padding and border in the element's total width and height */
  display: inline-block; /* Allows multiple module-boxes to sit side-by-side */
  vertical-align: top; /* Align modules to the top */
}

.module-title {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.module-description {
  margin-bottom: 15px;
}

.module-subtitle {
  font-size: 1.2em;
  margin-top: 20px;
  margin-bottom: 10px;
}

.module-item {
  border: 1px solid #eee;
  padding: 10px;
  margin-bottom: 5px;
}

.empty-message {
  font-style: italic;
  color: #888;
  margin-top: 10px;
}

.module-set-inputs,
.module-parameter-inputs {
  width:90%;
  margin-top: 20px;
}

.toggle-container {
  text-align: right;
  margin-bottom:10px;
}

/* Toggle Switch Styling (Example) */
.toggle-container label {
  display: inline-block;
  position: relative;
  width: 60px;
  height: 34px;
}

.toggle-container label input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-container label span {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
  border-radius: 34px;
}

.toggle-container label span:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

.toggle-container label input:checked + span {
  background-color: #2196F3;
}

.toggle-container label input:focus + span {
  box-shadow: 0 0 1px #2196F3;
}

.toggle-container label input:checked + span:before {
  transform: translateX(26px);
}